// app/eventos/[id]/page.tsx
import eventos from "../../lib/eventos";
import { Evento } from "../../lib/types";

type Props = {
  params: { id: string };
};

export default function EventoDetalhesPage({ params }: Props) {
  const evento: Evento | undefined = eventos.find((item: Evento) => item.id === params.id);

  return (
    <main className="page">
      <header className="pageHeader">
        <h1 className="pageTitle">{evento.titulo}</h1>
        <p className="pageSubtitle">
          {evento.data} • {evento.horario}
        </p>
      </header>

      <section className="detailsHero">
        <img
          className="detailsImage"
          src={evento.imagem}
          alt={evento.titulo}
        />

        <aside className="detailsPanel">
          <div className="pill">{evento.classificacao}</div>

          <h2 className="sectionTitle" style={{ marginTop: 0 }}>Informações</h2>

          <p className="text" style={{ marginTop: 10 }}>
          </p>

          <p className="text">
            <strong>Data:</strong> {evento.data}
          </p>

          <p className="text">
            <strong>Horário:</strong> {evento.horario}
          </p>

          <p className="text">
            <strong>Preço inicial:</strong> R$ {evento.precoInicial}
          </p>

          <div className="cardActions" style={{ marginTop: 14 }}>
            <button className="btn btnPrimary" type="button">
              Comprar
            </button>

            <button className="btn" type="button">
              Compartilhar
            </button>
          </div>

          {/* Se você tiver "sinopse" no type, descomenta isso */}
          {/* 
          <div className="section" style={{ marginTop: 16 }}>
            <h3 className="sectionTitle">Descrição</h3>
            <p className="text">{evento.sinopse}</p>
          </div>
          */}
        </aside>
      </section>

      {/* Extras: você pode mostrar mais detalhes embaixo */}
      {/* Exemplo: */}
      <section className="section">
        <div className="sectionHeader">
          <h2 className="sectionTitle">Sobre o evento</h2>
        </div>

        <div className="empty">
          <p className="text">
            Aqui você pode colocar descrição longa, regras, local, política de reembolso,
            mapa do lugar, etc.
          </p>
        </div>
      </section>
    </main>
  );
}
